.control {
  &__title {
    display: flex;
    align-items: center;
    & .content-block__more {
      margin-left: 0;
      & svg {
        margin-left: 0;
        margin-right: 10px;
        width: 30px;
      }
    }
    & h1 {
      font-size: 30px;
      margin-left: 40px;
    }
  }
  &__archive {
    & a {
      display: block;
      width: fit-content;
      color: #FF4D1C;
      text-decoration: underline;
      font-size: 18px;
      &:hover {
        color: #020202;
      }
      &:not(:first-child) {
        margin-top: 10px;
      }
    }
  }
  &__subtitle {
    font-size: 22px;
    font-weight: 500;
  }
  &__table {
    margin-top: 50px;
  }
  &__result {
    font-size: 22px;
    width: fit-content;
    margin-left: auto;
    margin-top: 30px;
    & b {
      font-weight: 500;
      font-size: 26px;
      color: #FF4D1C;
    }
  }
}

table.dataTable {
  margin: 0 !important;
  width: 100% !important;
}


