.vol_anketa_fields_item {
  display: grid;
  grid-template-columns: auto auto;
  gap: 50px;
  justify-content: space-between;
  align-items: center;
  &:not(:last-child) {
    margin-bottom: 15px;
    padding-bottom: 15px;
    border-bottom: 1px solid #aeaeae;
  }
  &_title {
    font-size: 18px;
    font-weight: 500;
    max-width: 500px;
    color: #333333;
  }
  &_value {
    font-size: 18px;
    font-weight: 400;
    max-width: 400px;
    text-align: end;
    color: #898989;
  }
}

fieldset{border:0!important;outline:none!important;margin:0!important;padding:0!important;}
.btn{display:inline-block;position:relative;padding:22px 66px;text-align:center;cursor:pointer;z-index:1;}
.btn:before{content:'';display:block;position:absolute;top:0;bottom:0;left:0;right:0;margin:auto;width:100%;height:100%;-webkit-border-radius:40px;-moz-border-radius:40px;border-radius:40px;background:#161310;z-index:-1;-webkit-transform:scale(1);-moz-transform:scale(1);-ms-transform:scale(1);-o-transform:scale(1);transform:scale(1);-webkit-transition:.3s;-o-transition:.3s;-moz-transition:.3s;transition:.3s;}
.btn:not([disabled]):hover:before{-webkit-transform:scale(0.95);-moz-transform:scale(0.95);-ms-transform:scale(0.95);-o-transform:scale(0.95);transform:scale(0.95);}
.vol_handling_item_title{font-size:20px;font-weight: 400; margin-bottom:20px;}
.vol_hand_item:not(:last-child){border-bottom:1px solid #aeaeae;padding-bottom:30px;margin-bottom:30px;}
.vol_handling{margin-top:50px;}
.vol_hand_item_form select{font-size:17px;padding:10px 20px;margin-right:20px;background:#fff;border:2px solid;}
.volhand_email_item:not(:last-child){margin-bottom:20px;}
@media only screen and (max-width: 767px){
  form{width:100%;}
  .btn{padding:16px 28px;}
  input{padding:12px 20px;}
}
@media only screen and (max-width: 767px){
  fieldset{min-width:initial;}
}

.vol_handling_item_title {
  & span {
    font-weight: 500;
  }
}

.volhand_email_item {
  display: flex;
  align-items: center;
  & .email__sent {
    display: block;
    font-size: 16px;
    color: #FF4D1C;
    margin-left: 10px;
  }
}
